html {
  font-size: 16px;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0rem;
  color: #494949;
  background-color: #f3f3f3;
  font-size: 1.25rem;
  align-items: center;
}

input,
button {
  font-family: inherit;
}

header,
main,
footer {
  width: 46.3rem;
}

header {
  display: flex;
  flex-direction: column;
  margin-top: 1.2rem;
  margin-bottom: 2rem;
  align-items: stretch;
}

header > .logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#logo {
  height: 2.8rem;
}

.logo > p {
  margin: 0;
  margin-top: 1.2rem;
  padding: 0;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.8rem;
  text-transform: uppercase;
}

main {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 1.4rem;
  flex-grow: 1;
}

main,
#other-issues,
.modalContent {
  border: 1px solid #cdcdcd;
  background-color: #fff;
}

.page:not([hidden]) {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.page > p {
  font-size: 0.9rem;
}

main h1 {
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
}

#typeSelectorGroup {
  font-size: 1rem;
}

#typeSelectorGroup > label {
  font-weight: 700;
}

#typeSelectorGroup > p {
  margin-top: 0.5em;
  margin-bottom: 1em;
  font-size: 0.9rem;
}

#typeSelectorGroup > p:first-of-type {
  margin-bottom: 30px;
}

html:not([dir="rtl"]) #typeSelectorGroup > p {
  margin-left: 30px;
}

html[dir="rtl"] #typeSelectorGroup > p {
  margin-right: 30px;
}

#anonymousSubmissionContainer {
  margin-top: 0.5em;
}

#anonymousSubmissionWarning,
#error {
  margin-top: 0.3em;
  margin-bottom: 1em;
  color: var(--color-error);
}

#comment {
  min-height: 2em;
  flex-grow: 1;
}

#sendingProgressContainer:not([hidden]) {
  display: flex;
  flex-direction: row;
  margin-top: 2rem;
  justify-content: center;
}

#result {
  border-width: 0px;
  flex-grow: 1;
}

#showData {
  margin: 0;
}

#showDataValue {
  overflow: auto;
  box-sizing: border-box;
  width: 100%;
  margin: 0 0 1rem;
  padding: 20px;
  border: 1px solid #ccc;
  font-size: 1rem;
  flex-grow: 1;
}

footer,
footer > div {
  box-sizing: border-box;
  padding-bottom: 16px;
}

footer > div:not(#other-issues) {
  display: flex;
  flex-direction: row;
  margin-top: 2rem;
}

footer > div:not(#other-issues) > div {
  flex-grow: 1;
  align-self: flex-end;
}

#privacyPolicy,
#other-issues {
  font-size: 1rem;
}

#other-issues {
  margin-top: 1rem;
  padding-top: 16px;
  background-image: url(/skin/icons/info-big.svg);
  background-repeat: no-repeat;
}

html[dir="ltr"] #other-issues {
  padding-left: 72px;
  background-position: 24px center;
}

html[dir="rtl"] #other-issues {
  padding-right: 72px;
  background-position: calc(100% - 24px) center;
}

#other-issues a,
#other-issues a:visited {
  font-weight: 600;
  text-decoration: none;
}

#privacyPolicy,
#privacyPolicy:visited,
#other-issues a,
#other-issues a:visited {
  color: #0797e1;
}

/*
 * Generic styles
 */

[data-invisible="true"] {
  visibility: hidden;
}

button {
  padding: 0.8rem 1.2rem;
  background-color: transparent;
  font-size: 1.125rem;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
  flex-shrink: 0;
}

html:not([dir="rtl"]) button {
  margin-left: 0.5rem;
}

html[dir="rtl"] button {
  margin-right: 0.5rem;
}

button.primary:not(.icon) {
  border: 0px;
  color: #fff;
  background-color: #0797e1;
}

button.primary:not([disabled]):not(.icon):hover {
  box-shadow: inset 0 0 0 3px #005d80;
}

button.primary[disabled]:not(.icon) {
  background-color: #5cbce1;
}

button.secondary {
  border: 1px solid #0797e1;
  color: #0797e1;
}

button.secondary:hover {
  box-shadow: inset 0 0 0 2px #0797e1;
}

button.link {
  padding: 0.2rem;
  border: 0px;
  color: #0797e1;
  background-color: transparent;
  font-weight: 400;
  text-decoration: underline;
  text-transform: none;
}

button.link:hover {
  color: #5cbce1;
}

button.link:disabled,
button.link:disabled:hover {
  color: #ccc;
  cursor: default;
}

input[type="text"],
input[type="email"],
textarea {
  border: 2px solid #0797e1;
  font-size: 1.25rem;
}

input[type="email"]:invalid {
  border-color: var(--color-error);
}

input[type="checkbox"],
input[type="radio"] {
  display: inline-block;
  width: 18px;
  height: 18px;
  margin: 0px 3px;
  padding: 0px;
  border: 0px;
  background-color: transparent;
  background-repeat: no-repeat;
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type="checkbox"] {
  background-image: url(/skin/icons/checkbox.svg?off#off);
}

input[type="checkbox"]:checked {
  background-image: url(/skin/icons/checkbox.svg?on#on);
}

input[type="radio"] {
  background-image: url(/skin/icons/radio.svg?normal#normal);
}

input[type="radio"]:hover {
  background-image: url(/skin/icons/radio.svg?hover#hover);
}

input[type="radio"]:checked {
  background-image: url(/skin/icons/radio.svg?selected#selected);
}

.modal:not([hidden]) {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 5rem;
  background-color: rgba(0, 0, 0, 0.5);
}

.modalContent {
  display: flex;
  overflow: auto;
  flex-direction: column;
  padding: 2rem;
  flex-grow: 1;
  align-items: flex-end;
}

[aria-hidden="true"] {
  display: none !important;
}

#notification {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  padding: 2px;
  opacity: 0.8;
  color: #4a4a4a;
  background-color: #d8d8d8;
  font-size: 1rem;
}

#notification-text {
  text-align: center;
  flex: 1;
}

.icon {
  padding: 0px;
  border: 0px;
  background-color: transparent;
}

.icon:hover {
  box-shadow: none;
}

.icon::before {
  display: block;
  border: 0.2rem solid transparent;
  background-repeat: no-repeat;
  content: "";
}

.close.icon::before {
  width: 1rem;
  height: 1rem;
}

.icon.close.tertiary::before {
  background-image: url(/skin/icons/close.svg?tertiary#tertiary);
}

.icon.close.tertiary:hover::before {
  background-image: url(/skin/icons/close.svg?tertiary-hover#tertiary-hover);
}

body[data-page="commentPage"] #continue {
  display: none;
}

#commentPage > label {
  font-size: 1rem;
  font-weight: 700;
}

#anonymousSubmissionContainer {
  font-size: 0.9rem;
}

input[type="checkbox"],
input[type="radio"] {
  vertical-align: top;
}
