io-steps {
  display: flex;
  position: relative;
  margin: 0;
  margin-top: 2rem;
  padding: 0;
  justify-content: space-between;
}

/* this element is used only to decorate via horizontal line */
io-steps::before {
  position: absolute;
  z-index: -1;
  top: 12px;
  width: 100%;
  height: 1px;
  background-color: #bcbcbc;
  font-size: 1px;
  line-height: 1px;
  content: " ";
}

html:not([dir="rtl"]) io-steps button,
html[dir="rtl"] io-steps button {
  margin: initial;
  padding: initial;
}

io-steps button {
  min-width: 80px;
  border: 0;
  outline: none;
  color: #0797e1;
  background: #f3f3f3;
  font-size: small;
  font-weight: initial;
  text-transform: inherit;
}

io-steps button::before {
  display: block;
  width: 24px;
  height: 24px;
  margin: auto;
  margin-bottom: 8px;
  border-radius: 12px;
  color: #fafbfd;
  background-color: #0797e1;
  font-size: 0.8rem;
  font-weight: 400;
  line-height: 24px;
  content: attr(data-value);
}

io-steps button:disabled::before {
  background-color: #9b9b9b;
}

io-steps button:disabled {
  color: #d8d8d8;
}

io-steps button.completed::before {
  content: "";
  animation: io-steps-completed 0.3s ease-in-out;
}

@keyframes io-steps-completed {
  0% {
    content: " ";
  }

  30% {
    font-size: 0;
    content: "";
    transform: scale(0.5);
  }
}
